.box {
  display: flex;
  gap: 12px;

  margin-bottom: 16px;
}

.avatar {
  width: 44px;
  height: 44px;

  background: #000;

  border-radius: 100%;
}

.name {
  margin-bottom: 2px;
}

.rating {
  display: flex;
  gap: 8px;
}

.comment {
  font-size: 16px;
  line-height: calc(24 / 16);
}
